.calendar {


    &__years {

        border       : 2px dashed #cacaca;
        border-top   : 0;
        border-bottom: 0;


        & .year {
            width          : 100%;
            display        : flex;
            justify-content: space-between;
            position       : relative;

            & .prompt {
                font-family   : "SF Mono";
                font-weight   : 300;
                font-style    : italic;
                font-size     : 12px;
                letter-spacing: -0.02em;
                text-align    : left;
                color         : #020403;
                padding-left  : 26px;
                background    : url('./../img/sprites/sprite.svg#icon_warning')no-repeat 0 50% /20px;
                line-height   : 20px;
            }

            &__slider {
                display    : flex;
                width      : 100%;
                align-items: center;
                padding    : 13px 20px 15px 20px;
            }

            &__number {
                margin-left   : 152px;
                font-family   : "SF Mono";
                font-weight   : bold;
                font-size     : 20px;
                letter-spacing: -0.02em;
                text-align    : left;
                color         : #020403;
                display       : flex;

                & .prev {
                    background: none;
                    border    : none;

                    img {
                        transform: rotate(-180deg);
                    }
                }

                & .next {
                    background: none;
                    border    : none;
                }

                & .number {
                    display      : inline-block;
                    text-align   : center;
                    padding-right: 40px;
                    padding-left : 40px;

                    &__track {
                        & .number {}
                    }
                }
            }

            &__filter {
                display        : flex;
                flex           : 0 0 108px;
                justify-content: space-between;

                & .btn {
                    width : 100%;
                    height: 100%;

                    &:focus {
                        outline   : none;
                        box-shadow: none;
                    }
                }

                & .reset {
                    flex       : 0 0 54px;
                    border-left: 2px dashed #cacaca;

                }

                & .setting {
                    flex       : 0 0 54px;
                    border-left: 2px dashed #cacaca;

                }
            }
        }

    }

    &__months {
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        flex-wrap      : wrap;
        border-right   : 2px dashed #cacaca;
        border-left    : 2px dashed #cacaca;

        & .month__item {
            padding      : 17px 16px;
            border       : 2px dashed #cacaca;
            border-left  : 0;
            border-bottom: 0;
            flex         : 1 0 auto;
            position     : relative;
            background   : none;
            transition   : all 0.3s linear;

            &.active {
                // font-weight: bold;
                background: #020403;
                color     : #01BCF2;

                & .badge {
                    color: #01BCF2;
                }
            }

            &:hover {
                background: #020403;
                color     : #01BCF2;

                & .badge {
                    color: #01BCF2;
                }
            }

            &:last-child {
                border-right: 0;
            }

            & .badge {
                position  : absolute;
                top       : 0;
                right     : 0;
                background: transparent;
            }
        }
    }

    &__days {
        display           : flex;
        align-items       : center;
        justify-content   : space-between;
        border            : 2px dashed #cacaca;
        padding           : 5px 8px;
        transition        : all 0.3s linear;
        // flex-wrap      : wrap;

        & .day__item {
            padding       : 5px;
            background    : none;
            border        : none;
            font-family   : "SF Mono";
            font-weight   : normal;
            font-size     : 16px;
            letter-spacing: -0.02em;
            text-align    : center;
            color         : #020403;
            flex          : 1 0 auto;
            position      : relative;

            & span {
                position: relative;
                z-index : 1;
            }

            &.active {
                font-weight: bold;
                background : #020403;
                color      : #01BCF2;
            }

            &:hover {
                background: #020403;
                color     : #01BCF2;
            }

            &.dot {
                position: relative;

                &::before {
                    content      : '';
                    display      : block;
                    width        : 10px;
                    height       : 10px;
                    border-radius: 50%;
                    background   : #EA4300;
                    position     : absolute;
                    top          : 8px;
                    right        : 8px;
                    z-index      : 1;
                }
            }
        }
    }
}

@media (max-width: 991px) {


    .calendar__months {
        flex-wrap   : wrap;
        border-right: 0;
    }

    .calendar__months .month__item:last-child {
        border-right: 2px dashed #cacaca;
    }

    .calendar__days {
        flex-wrap: wrap;
    }
}

@media (max-width: 776px) {
    .calendar__years .year {
        flex-wrap: wrap;
    }

    .calendar__years .year__filter .reset {
        border-left : 0;
        border-right: 2px dashed #cacaca;
    }

    .calendar__years .year__filter {
        flex      : 0 0 100%;
        border-top: 2px dashed #cacaca;

    }

    .calendar__years .year__number {
        margin-left: 20px;
    }
}