.header {
    font-family: 'SF Mono';

    &__top {
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        padding-top    : 42px;
        padding-bottom : 19px;

        &>.nav-link {
            padding-left: 0;
        }
    }


    & .nav-link {
        font-size  : 20px;
        line-height: 24px;
        color      : #020403;
        animation  : all 0.4s linear;
        display    : inline-flex;


        &:hover {
            font-weight: 700;
        }

        & .icon {
            display     : inline-flex;
            margin-right: 10px;
        }
    }

    & .nav-lang {
        & .nav-item {
            position: relative;

            &::before {
                content  : '/';
                font-size: 20px;
                display  : block;
                position : absolute;
                right    : -6px;
                top      : 50%;
                transform: translateY(-50%);
            }

            &:last-child::before {
                content: none;
            }

            & .nav-link {

                // transition: all 0.3s linear;
                &.active {
                    font-weight    : bold;
                    text-decoration: underline;
                }

                &:hover {
                    font-weight    : bold;
                    text-decoration: underline;
                }
            }
        }
    }



    &__middle {
        display        : flex;
        align-items    : center;
        justify-content: space-between;
        padding        : 33px 20px 22px 20px;
        border         : 2px dashed #CACACA;
        border-bottom  : none;
        flex-wrap      : wrap;
        overflow       : hidden;

        & .logo {
            display        : flex;
            align-items    : center;
            justify-items  : center;
            font-size      : 36px;
            line-height    : 47px;
            text-transform : uppercase;
            font-weight    : 700;
            color          : #020403;
            text-decoration: none;
            letter-spacing : -0.02em;

            & span {
                margin-right: 20px;

                &:nth-child(2) {
                    font-family: 'PTSerif';
                    width      : 180px;
                }
            }
        }

        & .nav-link {
            display    : inline-flex;
            align-items: center;

            & span {
                margin-right: 20px;
            }

            &:last-child {
                padding-right: 0;
            }
        }
    }


    &__bottom {
        display        : flex;
        justify-content: space-between;
        border         : 2px dashed #CACACA;
        border-bottom  : none;
        position       : relative;

        & .nav {
            padding-top   : 36px;
            padding-bottom: 36px;
            flex          : 1 1 100%;
            flex-wrap     : wrap;
            overflow      : hidden;

            &-link {
                cursor    : pointer;
                transition: all 0.4s linear;
                color     : #020403;

                &:hover {
                    background: #020403;
                    color     : #01BCF2;
                }
            }
        }

        & .search {
            width             : 106px;
            // flex           : 1 0 106px; // height         : 100px;
            display           : flex;
            align-items       : center;
            justify-content   : center;
            border-left       : 2px dashed #CACACA;

            & .btn {
                width : 100%;
                height: 100%;

                img {
                    transition: all 0.3s linear;
                }

                &:hover {
                    img {
                        transform: scale(1.2);
                    }
                }

                &:focus {
                    box-shadow: none;
                }
            }

            &__form {
                position        : absolute;
                width           : 0;
                height          : 100%;
                transition      : all 0.5s linear;
                bottom          : 0;
                left            : 0;
                // border-bottom: 2px dashed #cacaca;

                form {
                    height: 100%;
                }

                &.active {
                    width: calc(100% - 106px);

                    & .form-control {
                        padding: .575rem .75rem;
                    }
                }

                & .form-control {
                    height    : 100%;
                    transition: all 0.45s linear;
                    padding   : 0;
                    border    : none;
                    font-size : 20px;

                    &:focus {
                        outline   : none;
                        box-shadow: none;
                    }

                    &::placeholder {
                        font-size: 26px;
                    }

                }
            }
        }
    }
}


@media (max-width: 991px) {
    .header__middle {
        justify-content: center;
    }

    .header__middle .logo {
        margin: 0 auto;
    }

    .header__middle .nav {
        width          : 100%;
        justify-content: space-between;
    }

    .header__bottom {
        flex-direction: column;
    }

    .header__bottom .nav {
        flex         : none;
        position     : relative;
        border-bottom: 2px dashed #CACACA;
    }

    .header__bottom .search__form {
        height: auto;
    }

    .header__bottom .nav-link {
        flex           : 1 1 33%;
        justify-content: center;
    }

    .header__bottom .search {
        margin-left: auto;
    }


}

@media (max-width: 776px) {
    .header__bottom .nav-link {
        flex           : 1 1 50%;
        justify-content: center;
    }

}

@media (max-width: 556px) {
    .header__bottom .nav-link {
        flex           : 1 1 100%;
        justify-content: center;
    }

    .header__top {
        flex-direction: column;
    }
}