@import './pagination/pagination.scss';
@import './filter';
@import './social-list/social-list.scss';
@import './event-detail/event-detail.scss';

.event-calendar {
  padding-top      : 50px;
  // padding-bottom: 50px;
  border           : 2px dashed #cacaca;
  border-bottom    : none;
  border-top       : none;
  overflow         : hidden;

  & .row>div {
    border-right : 2px dashed #cacaca;
    margin-bottom: 50px;

    // &:last-child {
    //   border: none;
    // }
    &:last-child {
      border-right: 0;
    }

  }

  &.smaller {

    & .card-header {
      img {
        height: 345px;
      }
    }

    & .type {
      font-size  : 14px;
      line-height: 19px;
    }

    & .artist {
      font-size  : 20px;
      line-height: 26px;
    }

    & .place {
      font-size   : 16px;
      line-height : 21px;
      padding-left: 26px;
    }

    & .day,
    .time {
      font-size   : 16px;
      line-height : 21px;
      margin-right: 0;
      padding-left: 26px;
    }

    & .price {
      font-size   : 14px;
      line-height : 19px;
      padding-left: 26px;

      & span {
        margin: 0 4px;
      }
    }

  }

  &.event-list {
    // border-left: 0;


    &::before {
      content   : '';
      display   : block;
      width     : 5px;
      height    : 50px;
      position  : absolute;
      background: white;
      top       : 0;
      left      : 15px;
    }

    &::after {
      content   : '';
      display   : block;
      width     : 5px;
      height    : 50px;
      position  : absolute;
      background: white;
      bottom    : 0;
      left      : 15px;
    }

    & .row>div {
      margin-bottom : 0;
      padding-bottom: 50px;


      &:nth-child(3n) {
        border-right: none;
      }

      &:nth-child(3n+1) {
        border-left: 2px dashed #cacaca;
      }

      &:nth-last-child(1),
      &:nth-last-child(2),
      &:nth-last-child(3) {
        padding-bottom: 0;
        margin-bottom : 50px;
      }
    }
  }
}

.event-card {
  padding: 0 20px;
  border : none;
  height : 100%;


  &:hover {
    text-decoration: none;
  }

  & .card-header {
    padding   : 0;
    transition: all 0.3s linear;

    & img {
      height: 496px;
    }

    overflow: hidden;

    &:hover {
      box-shadow: 0 10px 10px rgba($color: #000000, $alpha: .30);
    }
  }

  & .card-body {
    padding-left : 0;
    padding-right: 0;
  }

  .type {
    font-family   : "SF Mono";
    font-weight   : normal;
    font-style    : italic;
    font-size     : 16px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    padding-left  : 20px;
    position      : relative;
    text-transform: capitalize;
    margin-bottom : 12px;

    &::before {
      content      : '';
      display      : block;
      width        : 10px;
      height       : 10px;
      border-radius: 50%;
      background   : #020403;
      position     : absolute;
      top          : 50%;
      left         : 0;
      transform    : translateY(-50%);
    }
  }

  .artist {
    font-family   : "PT Serif";
    font-weight   : bold;
    font-size     : 24px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    margin-bottom : 20px;
  }

  .place {
    font-family   : "SF Mono";
    font-weight   : normal;
    font-size     : 18px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    margin-bottom : 17px;
    padding-left  : 34px;
    background    : url('./../img/sprites/sprite.svg#icon_marker') no-repeat 0;
  }

  .date {
    display        : flex;
    justify-content: space-between;
    margin-bottom  : 17px;

  }

  .day {
    font-family   : "SF Mono";
    font-weight   : bold;
    font-size     : 18px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    padding-left  : 34px;
    background    : url('./../img/sprites/sprite.svg#icon_calendar') no-repeat 0/20px;

  }

  .time {
    font-family   : "SF Mono";
    font-weight   : normal;
    font-size     : 18px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    padding-left  : 34px;
    background    : url('./../img/sprites/sprite.svg#icon_cloack') no-repeat 0/20px;
    margin-right  : 64px;
  }

  .price {
    display: flex;

    font-family   : "SF Mono";
    font-weight   : normal;
    font-size     : 18px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    padding-left  : 34px;
    background    : url('./../img/sprites/sprite.svg#icon_price') no-repeat 0/20px;

    & span {
      margin: 0 15px;
    }

    & .start {}

    & .end {}
  }

  .card-footer {
    padding   : 0;
    background: none;

    & .btn-dark {
      border-radius : 0;
      display       : block;
      width         : 100%;
      padding-top   : 13px;
      padding-bottom: 13px;

      font-family   : "SF Mono";
      font-weight   : normal;
      font-size     : 20px;
      letter-spacing: -0.02em;
      color         : #01bcf2;
      border        : none;

      &:hover {
        background: #D83B01;
        color     : white;

        & path {
          fill: white
        }
      }

      path {
        transition: all 0.15s linear;
      }

      & .icon {
        margin-right: 10px;
      }

    }
  }

  &.horizontal {
    flex-direction   : row;
    border-left      : 2px dashed #cacaca;
    // border-right  : 2px dashed #cacaca;
    height           : auto;
    padding-right    : 0;

    & .card-body {
      padding-left : 30px;
      padding-right: 0;
      border-right : 2px dashed #cacaca;

    }

    & .date {
      flex-direction: column;

    }

    & .day {
      margin-bottom: 16px;
    }

    & .price {
      margin-bottom: 16px;
    }

    & .card-footer {
      border-top: none;

      & .btn-dark {
        width         : auto;
        font-family   : "SF Mono";
        font-weight   : normal;
        font-size     : 16px;
        letter-spacing: -0.02em;
        text-align    : left;
        color         : #fff;
        padding-left  : 44px;
        padding-right : 44px;

        svg {
          margin-right: 10px;
        }
      }
    }
  }
}

.section-title {
  display        : flex;
  justify-content: space-between;
  align-items    : center;
  padding        : 32px 30px 27px 21px;
  border         : 2px dashed #CACACA;
  overflow       : hidden;

  & .title {
    font-family   : "PT Serif";
    font-weight   : bold;
    font-size     : 30px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    text-transform: uppercase;

  }

  & .link {
    font-family   : "SF Mono";
    font-weight   : normal;
    font-size     : 18px;
    letter-spacing: -0.02em;
    text-align    : right;
    color         : #020403;
    padding-right : 34px;
    background    : url('./../img/sprites/sprite.svg#link__arrow-small') no-repeat 100% 50%/20px;
  }
}

.page-title {
  display        : flex;
  justify-content: space-between;
  align-items    : center;
  padding        : 32px 30px 27px 21px;
  border         : 2px dashed #CACACA;
  overflow       : hidden;

  & .title {
    flex          : 1 0 auto;
    font-family   : "PT Serif";
    font-weight   : bold;
    font-size     : 30px;
    letter-spacing: -0.02em;
    text-align    : left;
    color         : #020403;
    text-transform: uppercase;
    text-align    : center;
  }

  & .link {
    flex          : 0 1 auto;
    font-family   : "SF Mono";
    font-weight   : normal;
    font-size     : 18px;
    letter-spacing: -0.02em;
    color         : #020403;
    padding-left  : 82px;
    background    : url('./../img/sprites/sprite.svg#link_arrow-left')no-repeat;
  }

  & .form-control {
    width          : auto;
    flex           : 0 2 auto;
    border         : none;
    font-family    : "SF Mono";
    font-weight    : normal;
    font-size      : 18px;
    letter-spacing : -0.02em;
    text-align     : right;
    color          : #020403;
    appearance     : none;
    padding-right  : 36px;
    background     : url('./../img/sprites/sprite.svg#link_arrow-down')no-repeat 100% 50%/ 24px;
    cursor         : pointer;
    -moz-appearance: none;
    appearance     : none;
  }
}

.custom-checkbox {
  margin-bottom: 20px;


  & .custom-control {
    &-label {
      font-family   : "SF Mono";
      font-weight   : 600;
      font-size     : 20px;
      letter-spacing: -0.09 em;
      text-align    : left;
      color         : #020403;
      padding-left  : 10px;
      cursor        : pointer;

      &::before {
        width        : 25px;
        height       : 25px;
        border-radius: 0;
        border-width : 2px;
        border-color : #020403;
      }

      &::after {
        content         : none;
        background-image: none;
        width           : 11px;
        height          : 5px;
        border-left     : 2px solid #020403;
        border-bottom   : 2px solid #020403;
        transform       : rotate(-45deg);
        background      : transparent;
        border-color    : #020403;
        top             : 14px;
        left            : -18px;

      }
    }

    &-input {
      &:checked~.custom-control-label::before {
        background  : transparent;
        border-color: #020403;
      }

      &:checked~.custom-control-label {
        text-decoration: underline;
      }

      &:checked~.custom-control-label::after {
        content: '';
      }
    }
  }
}



@media (max-width: 991px) {
  .event-card .card-header img {
    height: initial;
  }

  .event-calendar .row>div {
    border: none;
  }

  // .event-card {
  //   padding-bottom: 50px;
  // }

  .event-calendar.smaller .card-header img {
    height: initial;
  }

  .event-calendar.smaller .row>div {
    border-right: 2px dashed #cacaca;

    &:nth-child(2n) {
      border-right: 0;
    }
  }

  .event-calendar.event-list::before,
  .event-calendar.event-list::after {
    content: none;
  }

  .event-calendar.event-list .row>div:nth-child(3n+1) {
    border-left: 0;

  }

  .event-calendar.event-list .row>div:nth-child(3n) {
    border-right: 2px dashed #cacaca;
  }

  .event-calendar.event-list .row>div:last-child {
    border-right: 0;
  }

  .event-calendar.event-list {
    border-left: 2px dashed #cacaca;

  }

  .page-title {
    flex-wrap: wrap;
  }

  .page-title .title {
    flex         : 1 1 100%;
    order        : 1;
    margin-bottom: 25px;
  }

  .page-title .link {
    order: 2;
  }

  .page-title .form-control {
    order: 3;
  }

  .filter {
    border-right: 2px dashed #cacaca;
  }
}

@media (max-width: 776px) {
  .section-title .title {
    font-size: 20px;
  }

  .event-calendar.smaller .row>div {
    border: 0;
  }

  .section-title .link {
    font-size: 12px;
  }

  .event-calendar.event-list .row>div:nth-child(3n) {
    border-right: 0;
  }

  .page-title .form-control {
    margin-left: auto;
  }
}