.social-list {
    margin-top: 40px;

    & .nav-link {
        border-radius  : 50%;
        width          : 44px;
        height         : 44px;
        display        : flex;
        align-items    : center;
        justify-content: center;
        margin-right   : 20px;
    }

    & .bg-facebook {
        background: #3B5998;
    }

    & .bg-telegram {
        background: #039BE5;
    }

    & .bg-twitter {
        background: #55ACEE;
    }

    & .bg-phone {
        background: #4CAF50;
        position  : relative;

        &::before {
            content     : '';
            display     : block;
            border      : 7px solid transparent;
            border-right: 15px solid #4CAF50;
            position    : absolute;
            bottom      : -5px;
            left        : -5px;
            transform   : rotate(-45deg);
        }
    }
}