.event-info {
    & .event-card.horizontal {
        padding-top: 50px;
    }

    &-desc {
        border-right : 2px dashed #cacaca;
        border-left  : 2px dashed #cacaca;
        padding-right: 20px;
        padding-left : 20px;

        & .__title {
            font-family   : "SF Mono";
            font-weight   : bold;
            font-size     : 36px;
            letter-spacing: -0.02em;
            color         : #020403;
            padding-top   : 35px;
            padding-bottom: 40px;
        }

        & .__description {

            font-family   : "SF Mono";
            font-weight   : normal;
            font-size     : 16px;
            letter-spacing: -0.01em;
            line-height   : 21px;
            color         : #020403;
            margin-bottom : 40px;
        }

        & .__video {
            width         : 100%;
            height        : 400px;
            padding-bottom: 60px;

            & iframe {
                height: 100%;
                width : 100%;
            }
        }
    }
}


@media (max-width: 991px) {
    .event-info .event-card.horizontal {
        border-right: 2px dashed #cacaca;
    }

    .event-card.horizontal .card-body {
        border-right: 0;
    }

    .event-info-detail {
        border-left: 2px dashed #cacaca;
    }

    .event-info-desc {
        border-bottom: 2px dashed #cacaca;
    }
}

@media (max-width: 776px) {
    .event-card.horizontal {
        padding-right : 20px;
        flex-direction: column;
    }
}