.filter {
    padding-left : 22px;
    padding-right: 5px;
    padding-top  : 43px;
    border-left  : 2px dashed #cacaca;
    height       : 100%;
    overflow     : hidden;

    &__title {
        display        : flex;
        justify-content: space-between;
        font-family    : "SF Mono";
        font-weight    : bold;
        font-size      : 36px;
        letter-spacing : -0.02em;
        text-align     : left;
        color          : #020403;
        margin-bottom  : 17px;

    }

    & .types {}

    &__type {
        padding-top: 37px;

        &:first-child {
            padding-top: 0;
        }

        &:last-child {
            border-bottom: 0;
        }

        & .title {
            font-family   : "SF Mono";
            font-weight   : bold;
            font-size     : 24px;
            letter-spacing: -0.02em;
            text-align    : left;
            color         : #020403;
            margin-bottom : 18px;
        }

        padding-bottom: 40px;
        border-bottom : 2px dashed #cacaca;
    }
}

@media(max-width: 991px) {
    .filter {
        padding-right: 22px;
    }

    .types {
        display      : flex;
        border-bottom: 2px dashed #cacaca;
    }

    .filter__type {
        padding-top: 0;
        flex       : 1 1 auto;
        border     : none;

    }
}

@media (max-width: 556px) {
    .custom-checkbox .custom-control-label {
        font-size: 14px;
    }
}