.footer {
    padding-bottom: 160px;
    position      : relative;
    overflow      : hidden;

    &::before {
        content   : '';
        display   : block;
        position  : absolute;
        width     : 645px;
        height    : 645px;
        background: url('./../img/footer__el.png')no-repeat;
        bottom    : -110px;
        right     : 80px;
        z-index   : -1;
    }


    & .container {
        // background: white;
    }

    &__top {

        display   : flex;
        border    : 2px dashed #CACACA;
        background: white;

        &__logo {
            display    : flex;
            align-items: center;
            color      : #020403;
            padding    : 46px 41px 44px 20px;
            overflow   : hidden;

            &:hover {
                text-decoration: none;
                color          : lighten($color: #020403, $amount: 40);

            }

            & span {
                margin-right: 15px;

                &:nth-child(2) {
                    font-family   : 'PTSerif';
                    font-size     : 30px;
                    line-height   : 39px;
                    text-transform: uppercase;
                    font-weight   : 700;
                    margin-right  : 0;
                    letter-spacing: -0.02em;
                }
            }
        }

        & .__callback {
            display        : flex;
            justify-content: space-between;
            align-items    : center;
            padding        : 32px 20px 31px 20px;
            border-top     : 2px dashed #CACACA;
            flex-wrap      : wrap;
            align-content  : space-between;
            overflow       : hidden;

            & .link-email {
                font-family   : "SF Mono";
                font-weight   : 600;
                font-size     : 20px;
                letter-spacing: -0.02em;
                text-align    : left;
                color         : #020403;

                & .icon {
                    margin-right: 20px;
                }
            }

            & .link-contact {
                font-family   : "SF Mono";
                font-weight   : 300;
                font-style    : italic;
                font-size     : 14px;
                letter-spacing: -0.02em;
                text-align    : right;
                color         : #020403;

                & .icon {
                    margin-left: 5px;
                }
            }
        }

        & .info {
            flex     : 1 1 auto;
            max-width: 592px;

        }

        & .social {
            flex              : 1 1 auto;
            max-width         : 580px;
            display           : flex;
            flex-direction    : column;
            // align-items    : center;
            justify-content   : center;
            padding-left      : 67px;
            border-left       : 2px dashed #CACACA;

            &__link {
                font-family   : "PT Serif";
                font-weight   : bold;
                font-size     : 30px;
                letter-spacing: -0.02em;
                text-align    : left;
                color         : #020403;

            }
        }
    }

    &__bottom {
        display   : flex;
        border    : 2px dashed #CACACA;
        border-top: none;
        background: white;

        &__logo {
            flex           : 1 1 auto;
            max-width      : 286px;
            display        : flex;
            align-items    : center;
            justify-content: center;
            border-right   : 2px dashed #CACACA;
            padding        : 0 15px;
            overflow       : hidden;
        }

        & .footer__body {
            flex     : 1 1 auto;
            max-width: 880px;

            & .copyright {
                display        : flex;
                justify-content: space-between;
                align-items    : center;
                padding-left   : 36px;
                padding-right  : 54px;
                font-family    : "SF Mono";
                font-weight    : 300;
                font-style     : italic;
                font-size      : 18px;
                letter-spacing : -0.02em;
                text-align     : right;
                color          : #020403;
                border-bottom  : 2px dashed #CACACA;
                flex-wrap      : wrap;
                overflow       : hidden;
            }

            & .footer__nav {
                display        : flex;
                justify-content: space-around;
                padding        : 38px 20px;
                flex-wrap      : wrap;
                overflow       : hidden;

                & .nav-link {
                    font-family   : "SF Mono";
                    font-weight   : 600;
                    font-size     : 20px;
                    letter-spacing: -0.02em;
                    text-align    : left;
                    color         : #020403;
                    cursor        : pointer;
                    padding       : .5rem 1rem;
                    transition    : all 0.3s linear;

                    &:hover {
                        background: #020403;
                        color     : #01BCF2;
                    }
                }
            }
        }
    }

    & .mistake {
        padding-top   : 20px;
        font-family   : "SF Mono";
        font-weight   : 300;
        font-style    : italic;
        font-size     : 14px;
        letter-spacing: -0.02em;
        text-align    : left;
        color         : #020403;

        & .container {
            background: transparent;
        }
    }
}

@media (max-width: 991px) {
    .footer__top {
        flex-direction: column;
    }

    .footer__top .info {
        flex     : 1 1 100%;
        max-width: 100%;
    }

    .footer__top .social {
        border-top     : 2px dashed #CACACA;
        border-left    : 0;
        max-width      : 100%;
        padding        : 15px;
        flex-direction : row;
        justify-content: space-between;
        overflow       : hidden;
    }
}

@media (max-width: 556px) {
    .footer__top .social {
        flex-direction: column;
    }

    .footer__bottom .footer__body .copyright {
        padding        : 15px;
        justify-content: center;
    }

    .footer__top .__callback .link-contact {
        margin-left: auto;
    }

    .footer__top__logo span:nth-child(2) {
        font-size: 24px;
    }

    .footer__bottom .footer__body .footer__nav .nav-link {
        font-size: 16px;
        flex     : 0 0 100%;
    }
}

@media (max-width: 374px) {
    body {
        min-width: 375px;
    }
}