.pagination {
    & .page {
        &-item {
            &:first-child {
                svg {
                    transform: rotate(-180deg);
                }
            }

            &.active {
                & .page-link {
                    background: #020403;
                    color     : #01BCF2;

                    path {
                        fill: #01BCF2;
                    }
                }
            }
        }

        &-link {
            padding       : 7px 15px 9px 15px;
            font-family   : "SF Mono";
            font-weight   : bold;
            font-size     : 18px;
            letter-spacing: -0.02em;
            text-align    : right;
            color         : #020403;
            transition    : all 0.3s linear;
            border        : none;

            & path {
                transition: all 0.3s linear;
            }

            &:hover {
                background: #020403;
                color     : #01BCF2;

                path {
                    fill: #01BCF2;
                }
            }


        }
    }
}