.event-info-detail {
    border-right: 2px dashed #cacaca;
    height      : 100%;
    padding-top : 50px;
    overflow    : hidden;

    &::after {
        content   : '';
        display   : block;
        width     : 8px;
        height    : 50px;
        background: white;
        position  : absolute;
        bottom    : 0;
        left      : -20px;
    }

    & .detail__item {
        padding-top     : 40px;
        padding-left    : 20px;
        padding-right   : 20px;
        // border-left  : 2px dashed #cacaca;


        &:first-child {
            padding-top: 0;
        }

        & .list {
            padding-left : 20px;
            padding-right: 20px;

            &>li {
                // display       : flex;
                // flex-direction: column;
                list-style   : none;
                position     : relative;
                margin-bottom: 20px;

                &::before {
                    content      : '';
                    display      : block;
                    width        : 7px;
                    height       : 7px;
                    border-radius: 50px;
                    background   : #020403;
                    position     : absolute;
                    top          : 7px;
                    left         : -15px;
                }

                span {
                    font-family   : "SF Mono";
                    font-weight   : normal;
                    font-style    : italic;
                    font-size     : 14px;
                    letter-spacing: -0.02em;
                    text-align    : left;
                    color         : #020403;
                    display       : block;
                    margin-bottom : 10px;
                    margin-top    : 10px;
                }

                b {
                    font-family   : "SF Mono";
                    font-weight   : bold;
                    font-size     : 16px;
                    letter-spacing: -0.02em;
                    text-align    : left;
                    color         : #020403;
                    display       : block;

                }
            }

        }
    }

    & .__title {
        font-family   : "SF Mono";
        font-weight   : bold;
        font-size     : 24px;
        letter-spacing: -0.02em;
        text-transform: uppercase;
        color         : #020403;
        margin-bottom : 15px;
    }

    & .__image {
        margin-bottom: 20px;
    }

    .type {
        font-family   : "SF Mono";
        font-weight   : normal;
        font-style    : italic;
        font-size     : 16px;
        letter-spacing: -0.02em;
        text-align    : left;
        color         : #020403;
        padding-left  : 20px;
        position      : relative;
        text-transform: capitalize;
        margin-bottom : 12px;

        &::before {
            content      : '';
            display      : block;
            width        : 10px;
            height       : 10px;
            border-radius: 50%;
            background   : #020403;
            position     : absolute;
            top          : 50%;
            left         : 0;
            transform    : translateY(-50%);
        }
    }

    .name {
        font-family   : "PT Serif";
        font-weight   : bold;
        font-size     : 24px;
        letter-spacing: -0.02em;
        text-align    : left;
        color         : #020403;
        margin-bottom : 20px;
    }

    .place {
        font-family      : "SF Mono";
        font-weight      : normal;
        font-size        : 16px;
        letter-spacing   : -0.02em;
        line-height      : 21px;
        text-align       : left;
        color            : #020403;
        padding-left     : 26px;
        background       : url('./../img/sprites/sprite.svg#icon_marker') no-repeat 0 0 /18px;
        // padding-right : 15px;
        margin-bottom    : 12px;
    }

    .phone {
        display       : flex;
        flex-direction: column;
        padding-left  : 26px;
        font-family   : "SF Mono";
        font-weight   : bold;
        font-size     : 16px;
        letter-spacing: -0.02em;
        text-align    : left;
        color         : #020403;
        background    : url('./../img/sprites/sprite.svg#icon_phone') no-repeat 0 0 /18px;


    }

    .divider {
        margin-top   : 40px;
        border-bottom: 2px dashed #cacaca;
    }

    #map {
        height: 345px;
    }
}